.certsBlock{
    background: #efefef;
    height: auto;
    padding-top: 80px;
    padding-bottom: 80px;
    width: 100%;
}

.certsBlock h1{
    font-size: 60px;
}

.certs{
    margin-top: 70px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 80px;
    grid-column-gap: 20px;
}

.cert{
    margin: 0 auto;
    width: 80%;
    height: auto;
    border-radius: 20px;
    filter: drop-shadow(0 0 5px #000);
    transition: filter 0.2s linear;
}

.cert img{
    display: block;
    width: 100%;
    opacity: 0.8;
    transition: opacity 0.2s linear;
}

.cert img:hover{
    opacity: 1;
}

.cert:hover{
    filter: drop-shadow(0 0 10px #000);
}


