.header {
    height: 900px;
    width: 100%;
    background-image: url('../../Data/images/itsme.png');
    background-repeat: no-repeat;
    background-color: #efefef;
}

.nicepic{
    margin: 0 auto;
    width: 600px;
    height: 40px;
    background-color: #323232;
    border-radius: 0px 0px 40px 40px;
    position: relative;
    top: -20px;
    animation: moveTopDiv 0.8s linear;
}

.elementsInMove{
    position: relative;
    top: 250px;
    animation: moveHeaderTexts 2.5s alternate;
    display: grid;
}

@media (min-width: 1024px) {
    .elementsInMove{
        left: 120px;
        width: 900px;
        grid-template-columns: 7fr 2fr;
        grid-template-areas: 'headtexts imagefrontend';
    }
    .img{
        display: block;
    }
    @keyframes moveHeaderTexts {
        0% {left: -900px;}
        30% {left: -900px;}
        100% {left: 120px;}
    }
    .h1{
        font-size: 60px;
    }

    .h2{
        font-size: 30px;
    }
    .nicepic{
        display: block;
    }
}

@media (max-width: 1024px) {
    .elementsInMove{
        left: 50px;
        min-width: 400px;
        max-width: 800px;
        grid-template-columns: 1fr;
        grid-template-areas: 'headtexts';
    }
    .img{
        display: none;
    }
    @keyframes moveHeaderTexts {
        0% {left: -900px;}
        30% {left: -900px;}
        100% {left: 50px;}
    }
    .h1{
        font-size: 60px;
    }

    .h2{
        font-size: 30px;
    }
    .nicepic{
        display: none;
    }
}

@media (max-width: 732px) {
    .h1{
        font-size: 40px;
    }

    .h2{
        font-size: 24px;
    }

}

@media (max-width: 518px) {
    .h1{
        font-size: 24px;
    }

    .h2{
        font-size: 18px;
    }

}


.img{
    margin-top: 50px;
    grid-area: imagefrontend;
    animation: imgblind 1s linear infinite;
}

@keyframes moveTopDiv {
    0%{top: -50px;}
    100% {top: -20px;}
}

@keyframes imgblind {
    0% {opacity: 0;}
    50% {opacity: 1;}
    100% {opacity: 0;}
}

.headtexts{
    grid-area: headtexts;
}

.h1, .h2{
    text-align: left;
}


/* */

@keyframes moveSocialLinks {
    0% {left: -500px;}
    70% {left: -500px;}
    100% {left: 20px;}
}

.socialLinks{
    position: relative;
    top: 550px;
    left: 20px;
    display: grid;
    grid-row-gap: 50px;
    height: 90px;
    animation: moveSocialLinks 4s alternate;
}

@media (min-width: 700px){
    .elementsInMove{
        min-width: 300px;
    }
    .socialLinks{
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        width: 500px;
    }
}

@media (min-width: 350px) and (max-width: 700px){
    .elementsInMove{
        max-width: 300px;
    }
    .socialLinks{
        grid-template-columns: 1fr 1fr 1fr;
        width:350px;
    }
}

@media (max-width: 349px) {
    @keyframes moveHeaderTexts {
        0% {opacity: 0}
        100% {opacity: 1}
    }
    .elementsInMove{
        left: 0px;
        margin: 0 auto;
    }
    .socialLinks{
        grid-template-columns: 1fr 1fr;
        width: 300px;
    }
}


.socialLink{
    opacity: 0.6;
    transition: opacity 0.2s linear;
}

.socialLink:hover{
    opacity: 1;
}



