.content {
    width: 100%;
    padding: 20px;
    display: grid;
    grid-column-gap: 50px;
}

@media (min-width: 1340px) {
    .content{
        grid-template-columns: 2fr 4fr;
        grid-template-areas: 'aboutme skills' 'portfolio learnfuture';
        height: 1700px;
    }
}

@media (max-width: 1339px) {
    .content{
        grid-template-columns: 1fr;
        grid-template-areas: 'aboutme' 'skills' 'portfolio' 'learnfuture';
    }
}

.aboutMe, .portfolio, .skills, .learnFuture{
    text-align: center;
    margin-top: 100px;
}

.aboutMe{
    grid-area: aboutme;
}

.portfolio{
    grid-area: portfolio;
}

.skills{
    grid-area: skills;
}

.learnFuture{
    grid-area: learnfuture;
}

.h1{
    font-size: 40px;
}

.contentAboutMe{
    width: 90%;
    height: 500px;
    margin: 0 auto;
    padding: 30px;
}

.contentAboutMe ul{
    list-style: none;
    font-size: 22px;
    font-weight: bolder;
}

.paragraph{
    margin-top: 50px;
    font-size: 20px;
    font-weight: bold;
}

.gridSkills{
    width: 90%;
    height: 600px;
    margin: 50px auto 0 auto;
    display: grid;
    grid-column-gap: 20px;
}

@media (min-width: 740px) {
    .gridSkills{
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media (max-width: 740px) {
    .gridSkills{
        grid-template-columns: 1fr 1fr;
    }
}



.skill{
    margin: 0 auto;
    width: 150px;
    height: 200px;
    background-size: 80%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    display: flex;
    opacity: 0.7;
    transition: opacity 0.2s linear;
}

.skill::after{
    background-color: #323232;
    height: 40px;
    position: relative;
    top: -25px;
    border-radius: 10px;
    color: #fff;
    display: flex;
    padding: 5px;
    align-items: center;
    opacity: 0;
    transition: opacity 0.2s linear;
    margin: 0 auto;
}
.skill:hover::after{opacity: 0.8;}

.skill:hover{opacity: 0.9;}


