.links{
    margin: 0 auto;
    width: 100%;
}

.linkToSite{
    width: 60%;
    height: 50px;
    background: #323232;
    display: flex;
    align-items: center;
    color: #fff;
    text-decoration: none;
    justify-content: center;
    margin: 30px auto 0;
    border-radius: 10px;
    font-size: 20px;
    transition: width 0.2s ease-in-out, opacity 0.2s ease-in;
    opacity: 0.6;
}

.linkToSite a{
    color: #fff;
}

.linkToSite:hover{
    width: 70%;
    opacity: 0.8;
    transition: width 0.2s ease-in-out, opacity 0.2s ease-out;
}

.siteInProcess{
    color: #fff;
}